import "WorkPage.css"
import Arrow from 'Arrow'
import workData from 'workData'
import YoutubeIframe from 'YoutubeIframe'

let getWorkData = (id) => {
    let filtered = workData.filter(e => {return e.id == id})
    return filtered[0]
}

export default class WorkPage extends React.Component {
    constructor(p){
        super()
        this.facepic
        this.state={
            nextData: null,
            data: null,
            imageIsReady: false,
            facePicClass: null,
        }
    }
    

    componentDidMount(){
        window.scrollTo(0,0)
        let {match:{params}} = this.props
        let nowData = getWorkData(params.id)
        if( params.id == workData.length ){
            let nextData = getWorkData(1)
            this.setState({...this.state, nextData: nextData, data: nowData})
        }
        else if( params.id == 1 ){
            let nextData = getWorkData(Number(params.id) + 1)
            this.setState({...this.state, nextData: nextData, data: nowData})
        }
        else{
            
            let nextData = getWorkData(Number(params.id) + 1)
            this.setState({...this.state, nextData: nextData, data: nowData})
        }
        if(nowData.person.length == '1'){
            this.setState({facePicClass: 'one'})
        }else if(nowData.person.length == '2'){
            this.setState({facePicClass: 'two'})
        }else if(nowData.person.length == '3'){
            this.setState({facePicClass: 'three'})
        }else{
            this.setState({facePicClass: 'threeup'})
        }
    }

    componentWillMount(){
        let {match:{params}} = this.props
        let nowData = getWorkData(params.id)
        if( params.id == workData.length ){
            let nextData = getWorkData(1)
            this.setState({...this.state, nextData: nextData, data: nowData})
        }
        else if( params.id == 1 ){
            let nextData = getWorkData(Number(params.id) + 1)
            this.setState({...this.state, nextData: nextData, data: nowData})
        }
        else{
            let nextData = getWorkData(Number(params.id) + 1)
            this.setState({...this.state, nextData: nextData, data: nowData})
        }
    }
    componentWillReceiveProps(np){
        scrollTo(0,0)
        let {match:{params}} = this.props
        if( np.match.params.id !== params.id && np.match.params.id == workData.length ){
            let nowData = getWorkData(workData.length)
            let nextData = getWorkData(1)
            this.setState({...this.state, nextData: nextData, data: nowData})
            if(nowData.person.length == '1'){
                this.setState({facePicClass: 'one'})
            }else if(nowData.person.length == '2'){
                this.setState({facePicClass: 'two'})
            }else if(nowData.person.length == '3'){
                this.setState({facePicClass: 'three'})
            }else{
                this.setState({facePicClass: 'threeup'})
            }
        }
        else if( np.match.params.id == 1 ){
            let nowData = getWorkData(np.match.params.id)
            let nextData = getWorkData(Number(np.match.params.id) + 1)
            this.setState({...this.state, nextData: nextData, data: nowData})
            if(nowData.person.length == '1'){
                this.setState({facePicClass: 'one'})
            }else if(nowData.person.length == '2'){
                this.setState({facePicClass: 'two'})
            }else if(nowData.person.length == '3'){
                this.setState({facePicClass: 'three'})
            }else{
                this.setState({facePicClass: 'threeup'})
            }
        }
        else if( params.id !== np.match.params.id ){
            let nowData = getWorkData(np.match.params.id)
            let nextData = getWorkData(Number(np.match.params.id) + 1)
            this.setState({...this.state, nextData: nextData, data: nowData})
            if(nowData.person.length == '1'){
                this.setState({facePicClass: 'one'})
            }else if(nowData.person.length == '2'){
                this.setState({facePicClass: 'two'})
            }else if(nowData.person.length == '3'){
                this.setState({facePicClass: 'three'})
            }else{
                this.setState({facePicClass: 'threeup'})
            }
        }
    }
    render() {
        let { location, name, person, facepic, info, content, videoOpenning, videoEndding } = this.state.data
        let { nextData, imageIsReady, facePicClass } = this.state
        return  <section className='page work-page'>
                
                <div className="work-main-intro">
                    <div className="cover">
                        <img src={`${process.env.PUBLIC_PATH}asset/${location}/main.jpg`}/>
                    </div>
                    <div className="title">
                        <h1>{name}</h1>
                    </div>
                    <div className="info">
                        <p>{info}</p>
                    </div>
                    <div className="designer">
                        <div className="name">
                            <span className='en'>Designer /</span>
                            {person.map((e, id) => <span key={id} className='cn'>{e}</span>)}
                        </div>
                        <div className={`facepic ${facePicClass}`}>
                            { facepic.map((e,id) => 
                                <div className="pic-item" key={id}>
                                     <img src={`${process.env.PUBLIC_PATH}asset/${location}/${e}.jpg`}/>
                                </div> 
                            ) }
                        </div>
                    </div>
                </div>
                <div className="work-article-content">
                    { videoOpenning == null ? null : <YoutubeIframe
                    poster={`${process.env.PUBLIC_PATH}asset/${location}/${videoOpenning.location}`}
                    posterClassname={'cover'}
                    width={1920}
                    height={1080}
                    iframeId={'videoIframe'}
                    containerClassname={'video'}
                    youtubeSrc={videoOpenning.youtubeSrc}
                /> }
                    <div className="work-content" dangerouslySetInnerHTML={{__html: content}}/>
                    { videoEndding == null ? null : <YoutubeIframe
                    poster={`${process.env.PUBLIC_PATH}asset/${location}/${videoEndding.location}`}
                    posterClassname={'cover'}
                    width={1920}
                    height={1080}
                    iframeId={'videoIframe'}
                    containerClassname={'video'}
                    youtubeSrc={videoEndding.youtubeSrc}
                /> }
                </div>
                <div className="next-work-notifi">
                    
                    <div className="left-deco">
                        <img src={`${process.env.PUBLIC_PATH}asset/next-work-dot-1.png`} />
                    </div>
                    <Link to={`/works/${nextData.id}`}>
                    <div className="next-work-title">
                        <span className='en'>NEXT WORK</span>
                        <span className='next-work-name'>{nextData.name}</span>
                    </div>
                    
                    <div className="next-work-cover">
                        <img src={`${process.env.PUBLIC_PATH}asset/${nextData.location}/circle.jpg`} />
                    </div>
                    </Link>
                    <div className="right-deco">
                        <img src={`${process.env.PUBLIC_PATH}asset/next-work-dot-2.png`} />
                    </div>
                    
                </div>
            </section>
        
    }
}


